import { Component, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { Observable } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';
import { environment, eMenuClient } from 'src/environments/environment';
import { Router } from '@angular/router';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.scss'],
})
export class MainNavComponent implements OnInit {
  __localStorage = localStorage.getItem(environment.key_storage.MENU.CLIENT);
  __eMenu = eMenuClient;
  // __eMenu = eMenu
  _title = 'ĐHBK - Quản lý Phòng thí nghiệm'; //environment.MENU_MUON_THIET_BI_TITLE
  // __idMenu: number
  _displayname = 'Menu';
  isHandset$: Observable<boolean> = this.breakpointObserver
    .observe(Breakpoints.Handset)
    .pipe(
      map((result) => result.matches),
      shareReplay()
    );

  constructor(
    private breakpointObserver: BreakpointObserver,
    private router: Router,
  ) {}
  ngOnInit() {
    // this.__idMenu = this.__eMenu.THONG_TIN_CO_BAN
    if (!localStorage.getItem(environment.key_storage.TOKEN.CLIENT)) {
      this.showLogin();
    } else {
      this._displayname = localStorage.getItem(
        environment.key_storage.DISPLAY_NAME.CLIENT
      );
      if (this._displayname.length > 17) {
        this._displayname = this._displayname.slice(0, 16) + '...';
      }
    }
    console.log(this.__localStorage)
  }
  showLogin() {
    this.router.navigate(['login']);
  }
  showHomePage() {
    this.router.navigate(['/']);
  }
  logout() {
    localStorage.removeItem(environment.key_storage.TOKEN.CLIENT)
    this.showHomePage();
  }
}
